/* 이미지 영역 */

.image_wrap {
  position: relative;

}

.image_wrap::after {
  content: '';
  display: block;
  clear: both;
}

.backgroundDivBefore, .backgroundDivAfter {

  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  transition: all 0.3s;
}

.backgroundArea>img {
  width: 100%;
}

.backgroundtoggle {
  opacity: 1;

}

.backImg {
  width: 100%;
  position: absolute;
}

.actionArea {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 2;
  opacity: 0;
}



.actionArea > .content {
  transition: all 0.2s;
  border-color: #00000080;
  border-style: solid;

}

/* 하단 메세지 표시 영역 */
.alert {
  position: fixed;
  width: 94%;
  height: 9rem;
  background: linear-gradient(180deg, #01203e 0%, #000723 100%);
  bottom: 10px;
  left: 3%;
  right: 0px;
  z-index: 3;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
  display: flex;
  flex-direction: column;
  transform: translateY(150%);
  transition: all 0.5s ease-in-out;
  border-radius: 8px;
  /* border: 2px solid #888888; */
  background: rgba(0, 0, 0, 0.488);
  box-shadow: 0 8px 32px 0 rgba(31,38,135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}



.alert>.title {
  font-family: "Roboto", "Noto Sans KR", 'AppleGothicNeoSD', 'Apple SD 산돌고딕 Neo', 'Droid sans', sans-serif;
  color: #FFFFFF;
  /* color: #FFFFFF; */
  letter-spacing: -0.025em;
  padding-left: 1.1428571429rem;
  /* height: 3.4285714286rem; */
  line-height: 3.4285714286rem;
  font-size: 1.4285714286rem;
  font-weight: 600;
  padding-top: 0.5142857143rem;
}


.alert>.body {
  font-family: "Roboto", "Noto Sans KR", 'AppleGothicNeoSD', 'Apple SD 산돌고딕 Neo', 'Droid sans', sans-serif;
  font-weight: 400;
  font-size: 1.1428571429rem;
