}

.progressBar {
  width: 100%;
  height: 1.7857142857rem;
  background-color: rgb(0,115,240);
  position: relative;
  top: 0;
  left: 0;
  transition: all 0.3s ease-in-out;
}

.progressBar-root {
  position: relative;
  overflow: hidden;
  background-color: #ddd;
  height: 1.7857142857rem;
  /* margin-top: 20px; */
  margin-top: auto;
}

.progressBar-text {
  position: absolute;
  top: 0;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  line-height: 2rem;
  size: 2rem;
}

.progressBar-text .white {
  color: #FFFFFF;
}
.alerttoggle {
  transform: translateY(0);
  transition: all 0.5s ease-in-out 1s;

}

.congraturation {
  background: linear-gradient(0deg, rgb(0, 116, 240) 0%, rgb(0, 148, 240) 100%);
  position: absolute;
  width: 100%;
  height: 0%;
  z-index: 5;
  transition: all 0.3s ease-in-out 0.3s;
  color: #FFFFFF;
  font-size: 0px;
  opacity: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 8px;

}

.congraturationToggle {
  height: 100%;
  transition: all 0.3s ease-in-out 0.3s;
  opacity: 1;

}


.congraturationToggle>.title {

  text-align: center;
  transition: opacity 0.3s ease-in-out 0.3s;
  width: 100%;

  margin-top: 1.04285714286rem;
  color: #FFFFFF;
  font-size: 1.4285714286rem;
  font-family: 'Noto Sans KR';
  font-weight: 600;

}

.congraturationToggle>.body {

  text-align: center;
  transition: opacity 0.3s ease-in-out 0.3s;
  margin-top: 0.6142857143rem;
  padding: 0 0.7142857143rem;
  color: #FFFFFF;
  font-size: 1rem;
  font-family: 'Noto Sans KR';
  font-weight: 400;

}

.congraturationToggle>button {
  height: 2.25714286rem;
  text-align: center;
  transition: all 0.3s ease-in-out;
  width: calc(100% - 2.28rem);
  border-radius: .28571429rem;

  color: rgb(0, 115,240);
  background-color: rgb(255, 255, 255);
  font-size: 1rem;
  font-family: 'Noto Sans Korean';
  font-weight: 400;
  border: 0;
  opacity: 0;
  margin: auto 1.14285714rem 1.14285714rem 1.14285714rem;

}

.congraturationToggle>.toggle {
  opacity: 1;
}

