/* Tool-tip */
.arrow_wrap {

  position: absolute;
  animation: 0.5s arrow_position ease-out infinite alternate;
  
}

.arrow_wrap .left:after {
  left: 20% !important;
}

.arrow_wrap .right:after {
  left: 80% !important;
}

.arrow_box {
  position: relative;
  background: #0073F0;
  z-index: 5;
  color: #FFFFFF;
  border-radius: 6px;
  padding: 0.5rem;
  text-align: center;
  font-size: 1rem;
  box-shadow: 0 10px 30px #4148563D;
}

.arrow_wrap .bottom:after {
  top: 100%;
  left: 80%;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(0, 115, 240, 0);
  border-top-color: #0073F0;
  border-width: 18px;
  margin-left: -18px;
  
}

.arrow_wrap .top:after {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(0, 115, 240, 0);
  border-bottom-color: #0073F0;
  border-width: 18px;
  margin-left: -18px;
}

@keyframes arrow_position {
  0% {
    margin-top: 0%;

  }

  to {
    margin-top: -1.5%;
  }
}

