}

.exitAreaToggle {

  transform: translateY(0);
  transition-delay: 3s;
}
.exit {
  position: absolute;
  right: 0.7285714286rem;
  top: 0.7285714286rem;
  font-size: 1.4285714286rem;

}
.toggle {
  opacity: 1;
  display: block;
  transition: opacity 0.2s ease-in-out 1s;

}